import { Locale } from 'vant';
import Vue from 'vue';
import VueI18n from 'vue-i18n';
// import en from './en';
// import zh from './zh';
// import ja from './ja';
// import ko from './ko';

Vue.use(VueI18n);

let glang: Langs;
// let i18n: VueI18n;
export enum Langs {
  en = 'en',
  zh = 'zh',
  ja = 'ja',
  ko = 'ko',
}

const messages = {
  en: {},
  zh: {},
  ja: {},
  ko: {},
};

export const i18n = new VueI18n({
  messages,
});

export function langInit(): Langs {
  let slang = localStorage.getItem('lang');
  if (!slang) {
    slang = new URLSearchParams(location.search).get('lang');
  }
  let lang = Langs.en;
  if (slang) {
    if (slang.startsWith('zh')) {
      lang = Langs.zh;
      import('./' + lang).then((la) => {
        Locale.use('zh-CN', la.default);
        messages.zh = la.default;
      });

    } else if (slang.startsWith('ja')) {
      lang = Langs.ja;
      import('./' + lang).then((la) => {
        Locale.use(lang, la.default);
        messages.ja = la.default;
      });
    } else if (slang.startsWith('ko')) {
      lang = Langs.ko;
      import('./' + lang).then((la) => {
        Locale.use(lang, la.default);
        messages.ko = la.default;
      } );
    } else {
      lang = Langs.en;
      import('./' + lang).then((la) => {
        Locale.use(lang, la.default);
        messages.en = la.default;
      } );
    }
  } else {
    import('./' + lang).then((la) => {
      Locale.use(lang, la.default);
      messages.en = la.default;
    } );
  }
  localStorage.setItem('lang', lang);
  glang = lang;
  i18n.locale = lang;
  return lang;
}

export function setLang(lang: Langs): void {
  if (!lang) {
    lang = Langs.en;
  }
  switch (lang) {
    case Langs.en: {
      import('./' + lang).then((la) => {
        Locale.use(lang, la.default);
        messages.en = la.default;
      } );
      break;
    }
    case Langs.zh: {
      import('./' + lang).then((la) => {
        Locale.use('zh-CN', la.default);
        messages.zh = la.default;
      } );
    }
    case Langs.ja: {
      import('./' + lang).then((la) => {
        Locale.use(lang, la.default);
        messages.ja = la.default;
      } );
      break;
    }
    case Langs.ko: {
      import('./' + lang).then((la) => {
        Locale.use(lang, la.default);
        messages.ko = la.default;
      } );
      break;
    }
  }
  glang = lang;
  i18n.locale = lang;
  localStorage.setItem('lang', lang);
}

export function getLang(): Langs {
  return glang;
}
