export function stringToByteArray(str: string): any[] {
    let bytes = new Array();
    let stringLength, c;
    stringLength = str.length;
    for (let i = 0; i < stringLength; i++) {
        c = str.charCodeAt(i);
        if (c >= 0x010000 && c <= 0x10FFFF) {            //四字节
            bytes.push(((c >> 18) & 0x07) | 0xF0);
            bytes.push(((c >> 12) & 0x3F) | 0x80);
            bytes.push(((c >> 6) & 0x3F) | 0x80);
            bytes.push((c & 0x3F) | 0x80);
        } else if (c >= 0x000800 && c <= 0x00FFFF) {     //三字节
            bytes.push(((c >> 12) & 0x0F) | 0xE0);
            bytes.push(((c >> 6) & 0x3F) | 0x80);
            bytes.push((c & 0x3F) | 0x80);
        } else if (c >= 0x000080 && c <= 0x0007FF) {     //两字节
            bytes.push(((c >> 6) & 0x1F) | 0xC0);
            bytes.push((c & 0x3F) | 0x80);
        } else {
            bytes.push(c & 0xFF);                         //一字节
        }
    }
    return bytes;
}

export function intToByte(num: number): number {
    let b = num & 0xFF;
    let c = 0;
    if (b >= 128) {
        c = b % 128;
        c = -1 * (128 - c);
    } else {
        c = b;
    }
    return c;
}

export function translateToFixLengthByteArray(str: string, fixedLength: number): any[] {
    let originByteArray = stringToByteArray(str);
    let len = originByteArray.length;
    const maxLength = fixedLength - 1;
    let tempResultBytes = new Array();
    if (len >= maxLength) {
        tempResultBytes = originByteArray.slice(0, maxLength);
        let countByte = intToByte(maxLength);                       //最多15个
        tempResultBytes.unshift(countByte);                         //头部加上计数位
    } else {
        let lessLength = fixedLength - 1 - len;
        for (let i = 0; i < lessLength; i++) {
            tempResultBytes.push(0);
        }
        tempResultBytes = originByteArray.concat(tempResultBytes);
        let countByte = intToByte(len);                             //头部加上计数位
        tempResultBytes.unshift(countByte);
    }
    return tempResultBytes;
}

export function U8ArrayToFillEnd(originByteArray: Uint8Array, fixedLength: number): number[] {
    let tempResultBytes: number[] = new Array<number>(fixedLength);
    tempResultBytes.fill(0, 0);
    for (let i = 0; i < originByteArray.length; i++) {
        tempResultBytes[i] = originByteArray[i];
    }
    return tempResultBytes;
}

//uuid，给证书hash处用.目前无实际意义
export function generateUUID() {
    let d = new Date().getTime();
    if (window.performance && typeof window.performance.now === "function") {
        d += performance.now(); //use high-precision timer if available
    }
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = (d + Math.random() * 16) % 16 | 0;
        d = Math.floor(d / 16);
        return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
}

//检查地址格式，长度
export function checkAddressInfo(nativeAddress: string) {
    const fixAddressLength = 48;        // 标准地址长度 48位
    if ((nativeAddress !== null) && (nativeAddress !== undefined) && (nativeAddress !== "") && (nativeAddress.length === fixAddressLength)) {
        return true;
    }
    return false;
}

//0x开头十六进制，转string
export function hexCharCodeToStr(hexCharCodeStr: string) {
    let trimedStr = hexCharCodeStr.trim();
    let rawStr =
        trimedStr.substr(0, 2).toLowerCase() === "0x"
            ?
            trimedStr.substr(2)
            :
            trimedStr;
    let len = rawStr.length;
    if (len % 2 !== 0) {
        alert("Illegal Format ASCII Code!");
        return "";
    }
    let curCharCode;
    let resultStr = [];
    for (var i = 0; i < len; i = i + 2) {
        curCharCode = parseInt(rawStr.substr(i, 2), 16); // ASCII Code Value
        resultStr.push(String.fromCharCode(curCharCode));
    }
    return resultStr.join("");
}