Vue.use(NavBar).use(Icon).use(Divider);
@Component({
    components: {},
})
export default class Home extends Vue {
    private certOwn = new CertOwn();
    private value = '';
    private titleText = '';
    private isShowLanguagePicker = false;
    private columns = ['中文', 'ENGLISH', '日本語', '한국어'];
    private bodyHeight = 0;
    private certNumber = '';
    private cacheCertList: CertOwn[] = [];      //链上证书列表缓存

    public created(): void {
        {   // 注册 调用原生方法时，返回的调用方法
            (window as any).nativeChainAddressToJsResult = this.nativeChainAddressToJsResult;
            (window as any).nativeSignMsgToJsResult = this.nativeSignMsgToJsResult;
        }
    }

    public mounted() {
        this.$store.state.title = "钻石DApp";
        this.titleText = this.$store.state.title;
        this.bodyHeight = document.documentElement.clientHeight;
    }

    public async onClickSearch() {
        // step1: 将数据源（链上证书列表）保存在本地，再在其中进行搜索。
        if (this.cacheCertList.length < 1) { //本地没有证书信息列表,去获取
            this.cacheCertList = await this.loadChainCertList();
        }
        if (this.cacheCertList.length < 1) {
            console.log("cacheCertList.length===>" + this.cacheCertList.length);
            ToastUtil.fail("加载链上证书信息出问题了！~~~");
            return;
        }
        let targetCert;
        for (let i = 0; i < this.cacheCertList.length; i++) {
            if (this.cacheCertList[i].certGia.gia && (this.cacheCertList[i].certGia.gia === this.certNumber)) {
                targetCert = this.cacheCertList[i] as CertOwn;
                break;
            }
        }
        if (targetCert !== null && targetCert !== undefined) {
            this.certOwn = targetCert;
            this.$store.state.nowCertOwn = this.certOwn;
            this.$router.push('/frame/cert/detail');
            return;
        } else {
            console.log(' typeof targetCert=>' + typeof targetCert);
            ToastUtil.fail("无相关的证书，请重新检查该gia证书，是否已完成上链");
        }
        // todo  step2:链上找（弃用），暂时没提供接口，是否实现 待定。
        /*if (!this.certNumber) {
            ToastUtil.fail(this.$t('notifyMsg.nullCertNum').toString());
            notes.add({type: NoteType.Error, message: this.$t('notifyMsg.nullCertNum').toString()});
        } else {
            DiamondUtil.getInstance().findCertByGia(this.certNumber).then((cert: CertOwn | Status) => {
                console.log("load gia by there");
                if (cert as CertOwn) {
                    this.certOwn = cert as CertOwn;
                    this.$store.state.nowCertOwn = this.certOwn;
                    this.$router.push('/frame/cert/detail');
                } else {
                    window.console.log(cert);
                    ToastUtil.fail(this.$t('notifyMsg.errorCertNum').toString());
                    notes.add({type: NoteType.Error, message: this.$t('notifyMsg.errorCertNum').toString()});
                }
            });
        }*/
    }

    public async loadChainCertList(): Promise<CertOwn[]> {
        try {
            // let nativeAddress = "5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY";  // todo alice address
            this.cacheCertList = await DiamondUtil.getInstance().loadCertList(this.$store.state.nativeChainAddress);//
            this.$store.state.certList = this.cacheCertList;
        } catch (e) {
            console.log("certs[0].assembleJsonInfo()===>" + e.toString());
            ToastUtil.fail("加载链上证书信息出问题了！~~~");
        }
        return this.cacheCertList;
    }

    public onClickScan(): void {  // Dapp内部的 扫码签名功能
        CashboxJs.getInstance().callNativeQrScanAndPwdAndSignToQR();
    }

    public nativeSignMsgToJsResult(val: string): void {
        ToastUtil.show('nativeSignMsgToJsResult===>' + val);
        console.log("nativeSignMsgToJsResult info is:" + val + "||val.length===>" + val.length);
    }

    private nativeChainAddressToJsResult(address: string) {
        console.log("address is =>" + address);
        this.$store.commit('updateNativeChainAddress', address);
    }

    private onConfirmLanguage(value: string): void {
        this.value = value;
        const langs = {
            en: 'ENGLISH',
            zh: '中文',
            ko: '한국어',
            ja: '日本語',
        };
        if (value === langs.en) {
            setLang(Langs.en);
        }
        if (value === langs.zh) {
            setLang(Langs.zh);
        }
        if (value === langs.ko) {
            setLang(Langs.ko);
        }
        if (value === langs.ja) {
            setLang(Langs.ja);
        }
        this.isShowLanguagePicker = false;
    }

    public onCloseDApp(): void {
        CashboxJs.getInstance().callNativeGoBack();
    }

    public onClickRight(): void {
        this.$store.state.mainMenu.show = true;
    }

    public onClickMenu(path: string): void {
        this.$store.state.mainMenu.show = false;
        this.$router.push(path);
    }
}
