import Vue from 'vue';
import Router from 'vue-router';
import Home from '../views/search.vue';
//import findLast from "lodash/findLast";
Vue.use(Router);

const router = new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/', name: 'cert_search', component: Home, alias: '/search',
        },
        {
            path: '/original/entry', name: 'original_entry', component: () => import('../views/authorize_original/entry.vue'),
        },
        {
            path: '/frame', name: 'frame',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import('../views/entry_frame.vue'),

            children: [
                {
                    path: 'cert/gia_frame',
                    name: 'cert_gia_frame',
                    props: true,
                    component: () => import('../views/cert_to_chain/gia_frame.vue'),
                },
                {
                    path: 'cert/entry', name: 'cert_to_chain_entry', component: () => import('../views/cert_to_chain/entry.vue'),
                },
                {
                    path: 'cert/list', name: 'cert_list', component: () => import('../views/cert_list/cert_list.vue'),
                },
                {
                    path: 'cert/sign', name: 'cert_sign', component: () => import('../views/cert_to_chain/cert_scan_sign.vue'),
                },
                {
                    path: 'cert/confirm', name: 'cert_confirm', component: () => import('../views/cert_to_chain/confirm.vue'),
                },
                {
                    path: 'cert/process', name: 'cert_process', component: () => import('../views/cert_to_chain/process.vue'),
                },
                {
                    path: 'cert/detail', name: 'cert_detail', component: () => import('../views/cert_list/cert_detail.vue'),
                },
                // business
                {
                    path: 'business/entry', name: 'business_entry', component: () => import('../views/authorize_business/entry.vue'),  // todo rename 1
                },
                {
                    path: 'business/list', name: 'business_list', component: () => import('../views/business_list/business_list.vue'),
                },
                {
                    path: 'business/detail', name: 'business_detail', component: () => import('../views/business_list/business_detail.vue'),
                },
                {
                    path: 'business/confirm', name: 'business_confirm', component: () => import('../views/authorize_business/confirm.vue'),
                },
                {
                    path: 'business/process', name: 'business_process', component: () => import('../views/authorize_business/process.vue'),
                },

                // original
                {
                    path: 'original/confirm',
                    name: 'original/confirm',
                    component: () => import('../views/authorize_original/confirm.vue'),
                },
                {
                    path: 'original/process', name: 'original_process', component: () => import('../views/authorize_original/process.vue'),
                },
                {
                    path: 'original/auth_admin_record', name: 'auth_admin_record', component: () => import('../views/authorize_original/auth_admin_record.vue'),
                },
                {
                    path: 'original/auth_admin_detail', name: 'auth_admin_detail', component: () => import('../views/authorize_original/auth_admin_detail.vue'),
                },
                // own
                {
                    path: 'own/input', name: 'own_input', component: () => import('../views/own/own_input.vue'),
                },
                {
                    path: 'own/confirm', name: 'own_confirm', component: () => import('../views/own/confirm.vue'),
                },
                {
                    path: 'own/process', name: 'own_process', component: () => import('../views/own/process.vue'),
                },
                {
                    path: 'account_info/account_info',
                    name: 'account_info',
                    component: () => import('../views/account_info/account_info.vue'),
                },
            ],
        },
        {
            path: '/scaned', name: 'scaned', component: () => import('../views/scan/scaned.vue'), props: true,
        },
    ],
});
router.beforeEach((to, from, next) => {
    //const record = findLast(to.matched,record=>record.mata.authority); // 路由页面带mata参数authority区分身份
    //to.path !== "/user/login"
    next();
});
router.afterEach(() => {
});

export default router;
