// 定义返回状态
export enum Status {
    None = 'None',
    Success = 'Success',
    Cancel = 'Cancel',
    ErrParameters = 'ErrParameters',
}

export enum StatusChain {
    None = 'None',
    Sended = 'Sened', // 交易发送成功
    Pendding = 'Pendding', // 交易进入Pendding状态
    Confirm = 'Confirm', // 交易确认
    Error = 'Error', // 交易出错
}

export enum CertType {
    None = 0, // 不知道的类型
    Gia = 1, // gia 证书
    Other = 2, // 其它证书
}

