import {AuthOriginalTxModel} from '@/model/original_address_model';
import {CertOwn} from '@/model/certs_model';
import {Business} from "@/model/business_model";
import {AuthAdminModel} from "@/model/auth_admin_model";

export interface IState {
    name: string;
    total: number;
    isLogin: boolean;
    postList: object[];
    businessList: object[];
    certList: object[];
    title: string;
    mainMenu: object;
    authOriginalTxModel: object;
    nativeChainAddress: string;
    nowBusiness: object;
    nowCertOwn: object;
    nowAuthAdminRecord: object;
}

export const state: IState = {
    nativeChainAddress: "",                           // 记录原生钱包当前的地址
    name: '',
    total: 0,
    isLogin: false,
    postList: [],
    title: '',                                        // 各页面title
    mainMenu: {
        show: false,                                 // 右抽屉显示标识
    },
    businessList: [],                                 // 业务员列表
    certList: [],                                     // 证书列表
    authOriginalTxModel: new AuthOriginalTxModel(),   // 操作原始地址（管理员）时的数据模型
    nowBusiness: new Business(),                      // 当前操作的业务员
    nowCertOwn: new CertOwn(),                        // 当前正在操作的证书
    nowAuthAdminRecord: new AuthAdminModel()
};
