
// 授权类型
export enum AuthBusinessType {
    None = -1,
    Auth = 0,           //增加管理员
    Delete = 1          //删除管理员
}

// 授权状态： 是否完成了授权
export enum AuthBusinessStatus {
    SUCCESS = 1,
    FAILURE = 0,
    ERROR = -1
}

export class Business {
    public txid = '';                   // 上链时的交易id
    public authAddress = '';            // 授权地址：对business进行时授权的admin地址
    public name = '';                   // 定义的名字，不超过12个字符
    public address = '';                // 定义的名字，不超过12个字符
    public maxCount = -1;               // 授权总次数 && 初始授权总次数
    public leftCount = -1;              // 剩余可授权次数
    public authType = AuthBusinessType.Delete;      // 授权类型
    public nonce = 0;                   // 每次自加1，这样来解决重复提交签名的问题
    public signInfo = "";               //对操作管理员 签名授权后的信息
    public authState = AuthBusinessStatus.ERROR;              //授权状态
    public ts = 0;                      // 上链的时间戳， 由链确定
}
