import {Toast} from 'vant';
import {ToastOptions, ToastMessage} from 'vant/types/toast';
import 'vant/lib/toast/style';

class Toasts {
    public static New(): Toasts {
        return new Toasts();
    }

    public show(msg: string, duration: number = 5000) {
        Toast(msg, {duration});
    }

    public success(msg: string, duration: number = 5000) {
        Toast.success({message: msg, duration: duration});
    }

    public fail(msg: string, duration: number = 5000) {
        Toast.fail({message: msg, duration: duration});
    }
}

export const ToastUtil = Toasts.New();