import {IState} from '@/store/state';
import {AddressBaseModel, AuthOriginalTxModel} from '@/model/original_address_model';
import {Business} from "@/model/business_model";
import {CertSignStatus} from "@/model/certs_model";

export default {
    // 清空 旧原始地址 信息列表
    emptyOriginalAddress(state: IState) {
        (state.authOriginalTxModel as any).originalAddressModelList = [];
    },
    // 记录 原始(管理员)信息 到vuex state
    addOriginalAddressModel(state: IState, model: AddressBaseModel) {
        (state.authOriginalTxModel as any).originalAddressModelList.push(model);
    },
    // 更新操作 管理员数据模型
    updateAdminModel(state: IState, model: AuthOriginalTxModel) {
        const auth = state.authOriginalTxModel as AuthOriginalTxModel;
        auth.txAddress = model.txAddress;
        auth.txName = model.txName;
        auth.nowAdminAddress = model.nowAdminAddress;
        auth.authType = model.authType;
    },
    //保存、更新 钱包的链地址
    updateNativeChainAddress(state: IState, nativeChainAddress: string) {
        state.nativeChainAddress = nativeChainAddress;
    },
    //保存、更新 操作的业务员信息
    updateNowBusiness(state: IState, business: Business) {
        state.nowBusiness = business;
    },
    //更新当前证书签名状态
    updateNowCertSignState(state: IState, status: CertSignStatus) {
        (state.nowCertOwn as any).attachInfo.certSignStatus = status;
    },
    //更新当前证书签名的信息
    updateNowCertSignedInfo(state: IState, info: string) {
        (state.nowCertOwn as any).attachInfo.certSignedInfo = info;
    },
    //更新记录 那个地址提交的证书
    updateCertBusinessAddress(state: IState, address: string) {
        (state.nowCertOwn as any).attachInfo.businessAddress = address;
    },
    //添加关联到证书的 附加信息
    updateCertAttachInputData(state: IState, msg: string) {
        (state.nowCertOwn as any).attachInfo.inputData = msg;
    }
};
