import Vue from 'vue';
import App from './App.vue';
import router from './router/router';
import store from './store/store';

// import 常用组件
import {Notify, Popup, Icon, NavBar, Field, Row, Col, CellGroup, Cell, Picker, Search, Toast} from 'vant';
import {Langs, setLang, i18n, langInit} from '@/assets/i18n/lang';
import 'vant/lib/notify/style';
import 'vant/lib/toast/style';
// import Auth from './util/directives';
// Vue.use(Auth);
Vue.config.productionTip = false; // 控制:开发模式

Vue.use(Notify).use(Popup).use(Icon).use(NavBar).use(Field).use(Row).use(Col).use(CellGroup).use(Cell).use(Picker).use(Search).use(Toast);
langInit();
Vue.prototype.Local = (lang: Langs): void => {
    setLang(lang);
};

new Vue({
    router,
    store,
    i18n,
    render: (h) => h(App),
}).$mount('#app');
