// 原始地址列表 模型
export interface AddressBaseModel {
    index: string;
    name: string;
    address: string;
    signStatus: boolean;
    signInfo: string;
}

export default class OriginalAddressModel implements AddressBaseModel {
    public index = '';           // 区分地址 下标
    public name = '';            // 昵称
    public address = '';         // 地址
    public signStatus = false;  // 是否进行签名状态
    public signInfo = '';        // 对交易 进行签名后 的信息
}

// 授权类型
export enum AuthAdminType {
    None = -1,
    AddAdmin = 0,
    DeleteAdmin = 1
}

// 授权||取消 原始地址 交易数据结构
export class AuthOriginalTxModel {
    public txAddress = '';                // txAddress
    public txName = '';                   // 定义的名字，不超过12个字符
    public authType = AuthAdminType.None;      // 授权类型 默认None
    public nowAdminAddress = '';          // 申请签名的管理员地址
    public nowAdminName = '';             // 申请签名的管理员昵称
    public isSignEnough = false;          // 原始地址数量，判 比例2/3
    public originalAddressModelList = [];
}
