/*
* 因为要调用原生 NativeQrScan的类方法，ts编译不过，用cashboxjs.js实现替代
* */

let StatusCashbox = {
    Success: '',
    Cancel: '',
}

export class CashboxJs {

    constructor() {
        this.instance = null;
    }

    static getInstance() {
        if (!this.instance) {
            this.instance = new CashboxJs();
        }
        return this.instance;
    }



    callNativeQrScanToJs() {
        console.log("js begin to NativeQrScan==========>");
        NativeQrScanToJs.postMessage("这是从js回来调qrscan，测试用，可以加这");
    }

    callNativeQrScanAndPwdAndSignToQR() {
        NativeQrScanAndPwdAndSignToQR.postMessage("");
    }

    callNativeSignMsgToJs(json) {
        NativeSignMsgToJs.postMessage(json);
    }

    callNativeGoBack() {
        console.log("js begin to nativeGoBack==========>");
        NativeGoBack.postMessage("");
    }

    nativeScanResult(val) {
        console.log("nativeScanResult======val====>" + val.toString());
    }

}